import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useWhomGiven() {
  const whomGivenList = ref([]);
  const fetchWhomGivenList = async () => {
    const { data } = await InfoService.fetchWhomGivenList();
    whomGivenList.value = data;
  };
  onMounted(async () => {
    await fetchWhomGivenList();
  });

  return {
    whomGivenList,
  };
}
