import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useBasisDistributions() {
  const basisDistributions = ref([]);
  const fetchBasisDistributions = async () => {
    const { data } = await InfoService.fetchBasisDistributions();
    basisDistributions.value = data;
  };
  onMounted(async () => {
    await fetchBasisDistributions();
  });

  return {
    basisDistributions,
  };
}
