import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useROTMDirections() {
  const rotmDirections = ref([]);
  const fetchROTMDirections = async () => {
    const { data } = await InfoService.fetchROTMDirections();
    rotmDirections.value = data;
  };
  onMounted(async () => {
    await fetchROTMDirections();
  });

  return {
    rotmDirections,
  };
}
