import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useHealthCareFacility() {
  const healthCareFacility = ref([]);
  const fetchHealthCareFacility = async () => {
    const { data } = await InfoService.fetchHealthCareFacility();
    healthCareFacility.value = data;
  };
  onMounted(async () => {
    await fetchHealthCareFacility();
  });

  return {
    healthCareFacility,
  };
}
