import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useGuardianshipTypes() {
  const guardianshipTypes = ref([]);
  const fetchGuardianshipTypes = async () => {
    const { data } = await InfoService.fetchGuardianshipTypes();
    guardianshipTypes.value = data;
  };
  onMounted(async () => {
    await fetchGuardianshipTypes();
  });

  return {
    guardianshipTypes,
  };
}
