import {ref} from 'vue';
import InfoService from '../../services/info.service';

export default function useSpecialEducationList() {
  const specialEducationList = ref([]);
  const fetchSpecialEducationListByRegionId = async (val) => {
    const {data} = await InfoService.fetchSpecialEducationListByRegionId(val);
    specialEducationList.value = data;
  };

  return {
    specialEducationList,
    fetchSpecialEducationListByRegionId,
  };
}
