import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useDistributionTypes() {
  const distributionTypes = ref([]);
  const fetchDistributionTypes = async () => {
    const { data } = await InfoService.fetchDistributionTypes();
    distributionTypes.value = data;
  };
  onMounted(async () => {
    await fetchDistributionTypes();
  });

  return {
    distributionTypes,
  };
}
