<template>
  <div class="bg-white rounded-md p-3">
    <PageTitle>
      Markaz tomonidan nazoratsiz / qarovsiz taqsimlangan bolani tahrirlash
    </PageTitle>
    <Loading
      v-if="loading"
      v-model:active="loading"
      :can-cancel="false"
      :is-full-page="true"
      color="#4785FE"
      loader="dots"
    />
    <template v-else>
      <section v-if="selectedJuvenile" class="my-4">
        <h1 class="text-2xl my-4">
          Tahrirlanayotgan bolaning shaxsiy ma'lumotlari
        </h1>
        <div class="flex">
          <PassportCard
            :passport-info="selectedJuvenile"
          />
        </div>
      </section>
      <section class="my-4">
        <h1 class="text-2xl my-4">
          Taqsimot ma'lumotlari
        </h1>
        <AllocateJuvenileForm :clear-form-mode="clearForm" :initial-fields="initialFieldsFromServer" @submit="handleSubmit" />
      </section>
    </template>
  </div>
</template>

<script setup>
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import {ref, watch} from "vue";
import PassportCard from "../components/PassportCard";
import useAlert from "../composables/useAlert";
import AllocateJuvenileForm from "../components/AllocateJuvenileForm";
import PageTitle from "../components/LayoutItems/PageTitle";
import {useRoute} from "vue-router";
import JuvenilesService from "../services/juveniles.service";
import AllocateService from "../services/allocate.service";
import useErrorAlert from "../composables/useErrorAlert";
import useClearForm from "../composables/useClearForm";

const loading = ref(true)

const selectedJuvenile = ref(null)
const route = useRoute()
const {fetchJuvenileReportsInfoById} = JuvenilesService
const {alertIt} = useAlert()
const {alertThisError} = useErrorAlert()
const {clearForm,setClearForm} = useClearForm()
const {fetchJuvenilesAllocateInfoById,editAllocatedJuvenile} = AllocateService

const updateJuvenilesList = ref(false)
const refreshJuvenileList = ()=>{
  updateJuvenilesList.value = true
}
const initialFieldsFromServer = ref(null)

async function handleSubmit(formData){
  try {
    const { data:{message} } = await editAllocatedJuvenile(formData,selectedJuvenile.value.id)
    await alertIt(message , 'success')
    clearAllFields()
    refreshJuvenileList()
  } catch (e) {
    alertThisError(e)
  }
}

function clearAllFields(){
  selectedJuvenile.value = null
  setClearForm(true)
}


watch(()=>route.params.id,async (id)=>{
  if(id){
    loading.value = true;
    initialFieldsFromServer.value = (await fetchJuvenilesAllocateInfoById(route.params.id))?.distributed_info
    selectedJuvenile.value = (await fetchJuvenileReportsInfoById(route.params.id))
    loading.value = false;
  }
},{
  immediate:true
})



</script>

