import useSpecialEducationList from '../composables/selectbox-list/useSpecialEducationList';
import useCentersToRegions from '../composables/selectbox-list/useCentersToRegions';

const DISTRIBUTION_PARENT_TYPE = 1;
const DISTRIBUTION_GUARDIAN_TYPE = 2;
const DISTRIBUTION_ITM_TYPE = 3;
const DISTRIBUTION_ROTM_TYPE = 4;
const DISTRIBUTION_SEND_TO_MEDICAL_TYPE = 5;
const DISTRIBUTION_SEND_TO_CENTER_TYPE = 6;
const DISTRIBUTION_SEND_TO_FOREIGN_TYPE = 7;
const DISTRIBUTION_OTHER_TYPE = 8;

const BASIS_DISTRIBUTION_NOTARIAL = 6;
const BASIS_DISTRIBUTION_SSV = 5;
const LEVEL_KINDKSHIP_TEMPRORARY_GUARDIAN = 11;

const GUARDIAN_VASIY_TYPE = 1;
const GUARDIAN_HOMIY_TYPE = 2;
const GUARDIAN_PATRONAT_TYPE = 3;
const GUARDIAN_FARZANDLIK_TYPE = 4;

const searchParentsByPINFL = () => {
  alert('Qidirilmoqda...');
};

const emit = defineEmits(['submit']);
const props = defineProps({
  clearFormMode: {
    type: Boolean,
    default: false,
  },
  initialFields: {
    type: Object,
    default: function () {
      return {};
    },
  },
});

onMounted(() => {
  if (Object.keys(initialFields.value).length) {
    initializeFields(initialFields.value);
  }
});

const region = ref('');

const {regionsList} = useRegion();
const {distributionTypes} = useDistributionTypes();
const {basisDistributions} = useBasisDistributions();
const {levelsKindship} = useLevelsKindship();
const {guardianshipTypes} = useGuardianshipTypes();
const {whomGivenList} = useWhomGiven();
const {rotmDirections} = useROTMDirections();
const {fetchSpecialEducationListByRegionId, specialEducationList} = useSpecialEducationList();
const {scrollToError} = useScrollToError();
const {markazsList} = useMarkazs();
const {healthCareFacility} = useHealthCareFacility();
const {regionList: regionToCenterList, fetchPositions} = useCentersToRegions();
// const {regionList} = useCentersToRegions();
const initialBasisSendingFile = ref([]);

const {clearFormMode, initialFields} = toRefs(props);

watch(
  () => clearFormMode.value,
  (clearFormMode) => {
    if (clearFormMode === true) {
      resetForm();
    }
  }
);

function initializeFields(fields) {
  if (fields.basis_sending_file) {
    initialBasisSendingFile.value = [fields.basis_sending_file];
  }
  basis_distribution.value = fields.basis_distribution;
  distribution_type.value = fields.distribution_type;
  itm_region.value = fields.itm_region?.id;
  level_kinkdship.value = fields.level_kinkdship;
  organization_coach_name.value = fields.organization_coach_name;
  organization_directors_name.value = fields.organization_directors_name;
  organization_psyhologs_name.value = fields.organization_psyhologs_name;
  psyhology_condition.value = fields.psyhology_condition;
  rotm_type.value = fields.rotm_type;
  skills_hobbies.value = fields.skills_hobbies;
  type_guardianship.value = fields.type_guardianship;
}

const {handleSubmit, isSubmitting, resetForm} = useForm();

function onInvalidSubmit({errors}) {
  console.log(errors);
  scrollToError(Object.keys(errors)[0]);
}

const onSubmit = handleSubmit(async (values) => {
  console.log(values);
  const formData = new FormData();
  Object.entries(values).map(([key, value]) => {
    if (value) {
      formData.append(key, value);
    }
  });
  emit('submit', formData);
}, onInvalidSubmit);

const allowMarkazToTuman = computed(() => {
  const distributions = [DISTRIBUTION_PARENT_TYPE, DISTRIBUTION_GUARDIAN_TYPE, DISTRIBUTION_ITM_TYPE, DISTRIBUTION_ROTM_TYPE, DISTRIBUTION_SEND_TO_MEDICAL_TYPE, DISTRIBUTION_OTHER_TYPE];
  return distributions.includes(distribution_type.value);
});

const allowToAddROTM = computed(() => {
  return distribution_type.value === DISTRIBUTION_ROTM_TYPE;
});
const allowToAddITM = computed(() => {
  return distribution_type.value === DISTRIBUTION_ITM_TYPE;
});
const allowToAddParent = computed(() => {
  return distribution_type.value === DISTRIBUTION_PARENT_TYPE || distribution_type.value === DISTRIBUTION_OTHER_TYPE;
});
const allowToAddGuardian = computed(() => {
  return distribution_type.value === DISTRIBUTION_GUARDIAN_TYPE;
});
const allowToAddForeign = computed(() => {
  return distribution_type.value === DISTRIBUTION_SEND_TO_FOREIGN_TYPE;
});
const allowToAddMedical = computed(() => {
  return distribution_type.value === DISTRIBUTION_SEND_TO_MEDICAL_TYPE;
});
const allowToAddCenter = computed(() => {
  return distribution_type.value === DISTRIBUTION_SEND_TO_CENTER_TYPE;
});
const allowToAddAdditionalItmAndRotmFields = computed(() => {
  return distribution_type.value === DISTRIBUTION_ROTM_TYPE || distribution_type.value === DISTRIBUTION_ITM_TYPE;
});

const dynamicValidatorForAdditionalSendingFields = computed(() => {
  return allowToAddITM.value || allowToAddROTM.value
    ? yup.string().nullable().required(requiredValidatorMessage)
    : // .matches(/^[a-zA-Z\s.,]+$/, onlyLatinLetters)
      yup.string().nullable().notRequired();
  // .matches(/^[a-zA-Z0-9\s.,]+$/, onlyLatinLetters)
});
const dynamicValidatorForITMFields = computed(() => {
  return allowToAddITM.value ? yup.string().nullable().required(requiredValidatorMessage) : yup.string().nullable().notRequired();
});
const dynamicValidatorForROTMFields = computed(() => {
  return allowToAddROTM.value ? yup.string().nullable().required(requiredValidatorMessage) : yup.string().nullable().notRequired();
});
const dynamicValidatorForGuardianFields = computed(() => {
  return allowToAddGuardian.value ? yup.string().nullable().required(requiredValidatorMessage) : yup.string().nullable().notRequired();
});
const dynamicValidatorForParentsFields = computed(() => {
  return allowToAddParent.value ? yup.string().nullable().required(requiredValidatorMessage) : yup.string().nullable().notRequired();
});

const dynamicValidatorForSpecialEducation = computed(() => {
  return allowToAddITM.value ? yup.string().required(requiredValidatorMessage) : yup.string().notRequired();
});

const {value: distribution_type, errorMessage: distribution_typeError} = useField('distribution_type', yup.number().required(requiredValidatorMessage));

const {value: itm_region, errorMessage: itm_regionError} = useField('itm_region', dynamicValidatorForITMFields, {
  initialValue: null,
});
const {value: special_education, errorMessage: special_educationError} = useField('special_education', dynamicValidatorForSpecialEducation, {initialValue: ''});
const {value: rotm_type, errorMessage: rotm_typeError} = useField('rotm_type', dynamicValidatorForROTMFields, {
  initialValue: null,
});

const {value: level_kinkdship, errorMessage: level_kinkdshipError} = useField('level_kinkdship', dynamicValidatorForParentsFields, {
  initialValue: null,
});

const {value: type_guardianship, errorMessage: type_guardianshipError} = useField('type_guardianship', dynamicValidatorForGuardianFields, {
  initialValue: null,
});

const dynamicValidatorForMedicalFacilityFields = computed(() => {
  return allowToAddMedical.value ? yup.number().nullable().required(requiredValidatorMessage) : yup.number().nullable().notRequired(requiredValidatorMessage);
});

const {value: type_healthcare_facility, errorMessage: type_healthcare_facilityError} = useField('type_healthcare_facility', dynamicValidatorForMedicalFacilityFields, {
  initialValue: null,
});

const dynamicValidationForTraining = computed(() =>
  allowToAddCenter.value || allowToAddForeign.value || allowToAddMedical.value || allowToAddROTM.value || allowToAddITM.value
    ? yup.boolean().notRequired()
    : yup.boolean().required(requiredValidatorMessage)
);

const {value: is_training, errorMessage: is_trainingError} = useField('is_training', dynamicValidationForTraining);

const referenceTypeDynamicValidator = computed(() => {
  return is_training.value ? yup.mixed().nullable().required(requiredValidatorMessage) : yup.mixed().nullable().notRequired();
});

const {value: training_file, errorMessage: training_fileError} = useField('training_file', referenceTypeDynamicValidator, {
  initialValue: null,
});

const dynamicValidationForCenterOpinion = () => {
  return allowToAddCenter.value ? yup.mixed().nullable().required(requiredValidatorMessage) : yup.mixed().notRequired();
};
const dynamicValidationMarkazToCenter = () => {
  return allowMarkazToTuman.value ? yup.number().required(requiredValidatorMessage) : yup.number().notRequired();
};

const {value: center_opinion_file, errorMessage: center_opinion_fileError} = useField('center_opinion_file', dynamicValidationForCenterOpinion, {
  initialValue: null,
});

const {value: basis_distribution, errorMessage: basis_distributionError} = useField('basis_distribution', yup.number().required(requiredValidatorMessage), {
  initialValue: null,
});

const {value: monitoring_markaz_tuman, errorMessage: monitoring_markaz_tumanError} = useField('monitoring_markaz_tuman', dynamicValidationMarkazToCenter, {
  initialValue: null,
});

const {value: organization_directors_name, errorMessage: organization_directors_nameError} = useField('organization_directors_name', dynamicValidatorForAdditionalSendingFields);
const {value: organization_coach_name, errorMessage: organization_coach_nameError} = useField('organization_coach_name', dynamicValidatorForAdditionalSendingFields);
const {value: organization_psyhologs_name, errorMessage: organization_psyhologs_nameError} = useField('organization_psyhologs_name', dynamicValidatorForAdditionalSendingFields);

const {value: basis_sending_file, errorMessage: basis_sending_fileError} = useField('basis_sending_file', dynamicValidationForCenterOpinion, {
  initialValue: null,
});
const {value: psyhology_condition, errorMessage: psyhology_conditionError} = useField('psyhology_condition', yup.mixed().nullable().required(requiredValidatorMessage), {
  initialValue: null,
});
const {value: skills_hobbies, errorMessage: skills_hobbiesError} = useField('skills_hobbies', yup.mixed().nullable().required(requiredValidatorMessage), {
  initialValue: null,
});

// const {
//   value: health_condition,
//   // errorMessage: health_conditionError
// } = useField('health_condition', yup.mixed().nullable().required(requiredValidatorMessage),{
//   initialValue:null
// });

const dynamicValidationForForeignCountry = () => {
  return allowToAddCenter.value ? yup.string().required(requiredValidatorMessage) : yup.string().notRequired();
};

const {value: foreign_to_whom_given, errorMessage: foreign_to_whom_givenError} = useField('foreign_to_whom_given', dynamicValidationForForeignCountry, {
  initialValue: null,
});

const dynamicValidationForCenter = () => {
  return allowToAddCenter.value ? yup.string().required(requiredValidatorMessage) : yup.string().notRequired();
};

const {value: markaz, errorMessage: markazError} = useField('markaz', dynamicValidationForCenter);

const allowedToShowParentInfo = computed(() => {
  if (distribution_type.value === DISTRIBUTION_PARENT_TYPE || distribution_type.value === DISTRIBUTION_GUARDIAN_TYPE) {
    if (distribution_type.value === DISTRIBUTION_GUARDIAN_TYPE) {
      return (
        type_guardianship.value === GUARDIAN_VASIY_TYPE ||
        type_guardianship.value === GUARDIAN_HOMIY_TYPE ||
        type_guardianship.value === GUARDIAN_PATRONAT_TYPE ||
        type_guardianship.value === GUARDIAN_FARZANDLIK_TYPE
      );
    } else return true;
  } else return false;
});

const dynamicValidationForParentInfo = computed(() => (allowedToShowParentInfo.value ? yup.string().required(requiredValidatorMessage) : yup.string().notRequired()));

const {value: last_name, errorMessage: last_nameError} = useField('last_name', dynamicValidationForParentInfo, {initialValue: ''});

const {value: first_name, errorMessage: first_nameError} = useField('first_name', dynamicValidationForParentInfo, {initialValue: ''});

const {value: father_name, errorMessage: father_nameError} = useField('father_name', dynamicValidationForParentInfo, {initialValue: ''});

const {value: pinfl, errorMessage: pinflError} = useField('pinfl', dynamicValidationForParentInfo, {initialValue: ''});

watch(
  () => distribution_type.value,
  (val) => {
    if (val === DISTRIBUTION_OTHER_TYPE) {
      level_kinkdship.value = LEVEL_KINDKSHIP_TEMPRORARY_GUARDIAN;
      basis_distribution.value = BASIS_DISTRIBUTION_NOTARIAL;
    } else if (val === DISTRIBUTION_SEND_TO_MEDICAL_TYPE) {
      basis_distribution.value = BASIS_DISTRIBUTION_SSV;
    } else if (val === DISTRIBUTION_SEND_TO_CENTER_TYPE) {
      basis_distribution.value = 1;
    } else {
      if (basis_distribution.value) {
        console.log('basis_distribution');
        basis_distribution.value = undefined;
      }
    }
  }
);

const basisDistributionIsDisabled = computed(() => {
  return (
    (distribution_type.value === DISTRIBUTION_OTHER_TYPE && basis_distribution.value === BASIS_DISTRIBUTION_NOTARIAL) ||
    (distribution_type.value === DISTRIBUTION_SEND_TO_CENTER_TYPE && basis_distribution.value === 1) ||
    (distribution_type.value === DISTRIBUTION_SEND_TO_MEDICAL_TYPE && basis_distribution.value === BASIS_DISTRIBUTION_SSV)
  );
});
</script>

