import { onMounted, ref } from "vue";
import InfoService from "../../services/info.service";

export default function useLevelsKindship() {
  const levelsKindship = ref([]);
  const fetchLevelsKindship = async () => {
    const { data } = await InfoService.fetchLevelsKindship();
    levelsKindship.value = data;
  };
  onMounted(async () => {
    await fetchLevelsKindship();
  });

  return {
    levelsKindship,
  };
}
